.App {
  text-align: center;
  background-color: #f5f5f5;
  height: 100vh;
}

.App-logo {
  margin: 10px;
  animation: App-logo-spin infinite 20s linear;
  height: 30px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  max-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  margin-bottom: 10px;
}

.App-link {
  color: #61dafb;
}

a {
  text-decoration: none;
  &:visited {
    text-decoration: none;
    color: blue;
  }
  &:hover {
    text-decoration: none;
  }
  &:focus {
    text-decoration: none;
  }
  &:hover,
  &:active {
    text-decoration: none;
  }
}

ul {
  list-style: none;
}
li {
  display: block;
  margin-bottom: 5px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
